import * as React from 'react';
import { TBItemSlot } from '@ali/we-design-next';
import { ColorIconMenu } from './components/next/colorIconMenu';
import { StickerSwitchButton } from './components/next/stickerSwitch';
import { BiPlugin } from '../../types';
import { ColorBlocks } from '@ali/4ever-mo';


export default function createRenderHoverToolbar(
  config: any
): BiPlugin['renderHoverToolbar'] {
  const { locale = {} } = config;
  return (props, controller, getNode) => {
    const { buttonName, extraProps } = props;
    const getColorBlocks = () => {
      const node = getNode();
      return ColorBlocks.isColorBlocks(node) ? node : null;
    }
    if (!getColorBlocks()) return null;
    switch (buttonName) {
      case 'style':
        return (
          <TBItemSlot display='iconButton'>
            <ColorIconMenu
              controller={controller}
              getNode={getColorBlocks}
              locale={locale}
              {
                ...extraProps
              }
            />
          </TBItemSlot>
        );
      case 'stickerSwitch':
        return (
          <TBItemSlot display="iconButton" >
            <StickerSwitchButton controller={controller} getNode={getColorBlocks} locale={locale} />
          </TBItemSlot>
        );
      default: return null;
    }
  };
}
